//#region LOGO #
.header { 
    background-color: rgba(0, 0, 0, 0.56);
    background-size: cover;
    background-repeat: no-repeat;
    background-position: 50% 50%;
    font-family: 'Roboto', sans-serif;

    @media screen and (max-width: $mobile) {
        background-image: linear-gradient(to right, rgba(0, 0, 0, 0.56), rgba(0, 0, 0, 0.56)), url(/images/header/header-mob1х.jpg);

        @media screen and (min-device-pixel-ratio:2),
        (min-resolution: 2dppx) {
            background-image: linear-gradient(to right, rgba(0, 0, 0, 0.56), rgba(0, 0, 0, 0.56)), url(/images/header/header-mob2х.jpg);
        }
    }

    @media screen and (min-width: 321px) and (max-width: $tablet) {
        background-image: linear-gradient(to right, rgba(0, 0, 0, 0.56), rgba(0, 0, 0, 0.56)), url(/images/header/header-tab1х.jpg);

        @media screen and (min-device-pixel-ratio:2),
        (min-resolution: 2dppx) {
            background-image: linear-gradient(to right, rgba(0, 0, 0, 0.56), rgba(0, 0, 0, 0.56)), url(/images/header/header-tab2х.jpg);
        }
    }
    @media screen and (min-width: 769px) {
        background-image: linear-gradient(to right, rgba(0, 0, 0, 0.56), rgba(0, 0, 0, 0.56)), url(/images/header/header-desk1х.jpg);

        @media screen and (min-device-pixel-ratio:2),
        (min-resolution: 2dppx) {
            background-image: linear-gradient(to right, rgba(0, 0, 0, 0.56), rgba(0, 0, 0, 0.56)), url(/images/header/header-desk2х.jpg);
        }
    }
}

.header-box {
    display: flex;
    justify-content: space-between;
    padding-top: 40px;
    align-items: center;
}
.logo {
    text-decoration: none;
    display: flex;
    justify-content: center;
    align-items: center;
}
.logo-title {
    color: transparent;
    @media screen and (min-width: $tablet) {
        font-family: inherit;
        font-weight: 500;
        font-size: 30px;
        line-height: calc(35 / 30);
        color: getColor(primary-text-color);
        margin-left: 8px;
    }
}

//#endregion #

//#region NAVIGATION #
.nav { 
    display: flex;
    list-style: none;
}
.nav-item { 
    &:not(:last-child) {
            margin-right: 40px;
        }
}
.nav-link {
    font-family: inherit;
    font-weight: 500;
    font-size: 12px;
    line-height: calc(14 / 12);
    text-decoration: none;
    text-transform: uppercase;
    text-align: center;
    color: getColor(primary-text-color);
}
.nav-link-current {
    position: relative;

    &::after{
        content: '';
        position: absolute;
        display: block;
        width: 100%;
        height: 3px;
        border-radius: 1px;
        background-color: getColor(accent-color);
    }

}
//#endregion #

//#region SEARCH #
.search-form {
    position: relative;
    display: flex;
    padding-top: 54px;
    padding-bottom: 92px;   
    align-items: center;
    text-align: center;

    @media screen and (min-width: $tablet) {
        padding-top: 40px;
        padding-bottom: 80px;
        margin-left: 184px;
        margin-right: 184px;
    }
    @media screen and (min-width: $desktop) {
        padding-top: 40px;
        padding-bottom: 80px;
        margin-left: 310px;
        margin-right: 310px;
    }

}
.search-input { 
    height: 20px;
    width: 100%;
    padding-right: 15px;

    background-color: inherit;

    border-style: none;
    border-bottom: 0.5px solid getColor(primary-text-color);

    color: getColor(primary-text-color);
    
    &::placeholder {
        color: getColor(primary-text-color);
        font-family: inherit;
        font-weight: 400;
        font-size: 14px;
        line-height: calc(16 / 14);
    }
}
.search-btn { 
    position: absolute;
    top: 0x;
    right: 0;
    background-color: transparent;
}

//#endregion #